





























	import orderItem from '../../components/orderItem.vue'
	import goodsOrderItem from '../../components/goodsOrderItem.vue'
	import customOrderItem from '../../components/customOrderItem.vue'
	import ut from '../../utils/index.js';
	export default {
		props: ['type', 'comment', 'show'],
		components: {
			orderItem,
			goodsOrderItem,
			customOrderItem
		},
		data() {
			return {
				order_list: [],
				cancel_reason: [],
				goods_list: [],
				custom_list: [] // 订制商品
			}
		},
		watch: {
			comment() {
				this.init();
			},
			show() {
				if (this.show && wx.getStorageSync('token')) {
					this.init();
				}
// 				 else {
// 					this.order_list = [];
// 					this.goods_list = [];
// 					this.custom_list = [];
// 				}
			}
		},
		onLoad(option) {
			/* this.comment=option.comment
			let barTitle = "查看订单";
			if(this.type == 1) {
				barTitle = "查看订单";
			}else if(this.type == 2) {
				barTitle = "确认施工方案和价格";
			}else if(this.type == 3) {
				barTitle = "质量验收及付尾款";
			}
			wx.setNavigationBarTitle({
			  title: barTitle
			}) */
			this.init();
		},
		methods: {
			init() {
				if(this.comment != 1){
					this.order_list = [];
				}
				if(this.comment != 2){
					this.goods_list = [];
				}
				if(this.comment != 3){
					this.custom_list = [];
				}
				if (this.comment == 1) {
					this.getOrderList();
				} else if (this.comment == 2) {
					this.getOrderList1();
				} else {
					this.getCustomOrderList();
				}
			},
			getOrderList() {
				ut.request({
					data: {
						type: this.type
					},
					method: 'get',
					url: "service/order/list"
				}).then(data => {
					console.log(data)
					// 如果返回结果与当前列表相同，则不处理
					if(JSON.stringify(this.order_list) == JSON.stringify(data)){
						return;
					}
					this.order_list = data;
				})
			},
			getOrderList1() {
				ut.request({
					data: {
						type: this.type
					},
					method: 'get',
					url: "goods/order/goodsOrderList"
				}).then(data => {
					console.log(data)
					data.forEach(item => {
						item.orderGoods.forEach(item => {
							if (item.picture) item.picture = item.picture.split(',')[0];
						})
					})
					// 如果返回结果与当前列表相同，则不处理
					if(JSON.stringify(this.goods_list) == JSON.stringify(data)){
						return;
					}
					this.goods_list = data;
				})
			},
			getCustomOrderList() {
				ut.request({
					data: {
						type: this.type
					},
					method: 'get',
					url: "customze/store/order/list"
				}).then(data => {
					console.log(data)
					// 如果返回结果与当前列表相同，则不处理
					if(JSON.stringify(this.custom_list) == JSON.stringify(data)){
						return;
					}
					this.custom_list = data;
				})
			}
		}
	}
