
























	import orderItem from '../../components/customOrderItem.vue'
	import goodsOrderItem from '../../components/goodsOrderItem.vue'
	import ut from '../../utils/index.js';
	export default {
		props:['type','comment','show','refreshStatus'],
		components: {
            orderItem,
			goodsOrderItem
        },
		data() {
			return {
				order_list: [],
				cancel_reason: [],
				goods_list:[]
			}
		},
		watch:{
			type(){
				this.order_list = [];
				this.goods_list = [];
				this.init();
			},
			show(){
				if(this.show&&wx.getStorageSync('token')){
					this.init();
				}
			}
		},
		onLoad(option) {
			this.init();
		},
		methods: {
			init() {
				this.getOrderList();
			},
			refresh(){
				this.getOrderList();
				this.$emit('refreshStatus');
			},
			getOrderList() {
				ut.request({
					data: {
						type: this.type
					},
					method: 'get',
					url: "customze/store/order/list"
				}).then(data=>{
					// 数据相等时不处理
					if(JSON.stringify(data) == JSON.stringify(this.order_list)){
						return;
					}
					this.order_list = data;
				})
			}
		}
	}
